import userSpaceBg from '../../common/user-space/user-space-bg.vue';
import newHead from '../../common/common/new-head.vue';
export default {
	components: {
		userSpaceBg,
		newHead
	},
	data() {
		return {
			userImg: '../../static/demo/datapic/4.jpg',
			imgIdx: 4,
			isguanzhu: false,
			tabBarIndex: 0,
			swiperHeigh: 500,
			loadingMore:'加载更多',
			tabBars: [
				{ name: '主页', id: 'zhuye' },
				{
					name: '糗事',
					id: 'qiushi'
				},
				{
					name: '动态',
					id: 'dongtai'
				}
			]
		};
	},
	onLoad() {
		this.getWindowHeigh();
	},
	methods: {
		getWindowHeigh() {
			uni.getSystemInfo({
				success: res => {
					const h = res.windowHeight;
					this.swiperHeigh = h - uni.upx2px(500 + 110 + 80);
				}
			});
		},
		changeTabbarIdx(index) {
			this.tabBarIndex = index;
		},
		ontabchange(e) {
			let index = e.target.current || e.detail.current;
			this.tabBarIndex = index;
		},
		loadMore() {
			console.log('加载更多');
		}
	}
};
